import LandingPage from '../src/Components/LandingPage/LandingPage';
import Home from '../src/Components/Home/Home';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import Countries from '../src/Components/Country/Country';
import CountryDetail from '../src/Components/CountryDetail/countryDetail';
//import TurismActivity from './Components/TurismActivity/TurismActivity';


function App() {
  return (
    <Router >
      <Switch>
        <Route path="/" exact component={LandingPage} />
        <Route path="/home" component={Home} />
        <Route exact path="/countries" element={<Countries/>}/>
        <Route exact path="/countries/:id" element={<CountryDetail/>}/>
        {/* <Route path="/create-activity" element={<TurismActivity/>}/> */}
      </Switch>
    </Router>
  );
}

export default App;
